.footer_wrapper{
    padding: 50px 40px 20px;
    border-top: 1px solid #f1f1f1;
    background: #f5f5f7;

    .footer_text {
        flex-direction: column;
        margin-bottom: 5rem;
        align-items: center;
        .text {
            color: #777;
            margin: 0;
        }
        .text1 {
            font-weight: 400;
            font-size: 1.5rem;
            line-height: 2rem;
        }
        .text2 {
            font-weight: 600;
            font-size: 4.5rem;
            line-height: 1;
            color: #151515b5;
            margin-bottom: 10px;
        }
        .text3 {
            font-weight: 300;
            font-size: 0.875rem;
            line-height: 1;
        }
    }
    .footer_content{
        justify-content: space-between;
        align-items: center;
        width: 70%;
        margin: 0 auto 30px;
        padding: 25px 0 10px 0;
        border-top: 1px solid #ddd;

        .copy_rights {
            width: 50%;
            p{
                font-size: 12px;
                letter-spacing: 2px;
                color: #777;
                margin: 0;
            }
        }
        .footer_links {
            width: 50%;
            .list {
                margin: 0;
                padding: 0;
                display: flex;
                justify-content: flex-end;
                li {
                    list-style : none;
                }
            }
            .links {
                font-size: 0.875rem;
                letter-spacing: 2px;
                color: #777;
                text-decoration: none;
                transition: 0.3s ease;
                display: inline-block;
                padding: 0 35px;

                svg {
                    font-size: 1.5rem;
                    vertical-align: middle;
                }

                &::after {
                    display: block;
                    content: '';
                    border-bottom: solid 1px #000;
                    padding-bottom: 1%;
                    transform: scaleX(0);
                    transition: transform 300ms ease-in-out;
                }

                &:hover:after {
                    color: #133154;
                    transform: scaleX(1);
                }
            }
        }
    }
}